import DataTable from 'react-data-table-component';
import {useState,useEffect} from 'react'
import axios from 'axios';
import 'bootstrap/dist/css/bootstrap.min.css';


<link rel="styleSheet" href="https://cdn.jsdelivr.net/npm/bootstrap@4.0.0/dist/css/bootstrap.min.css" integrity="sha384-Gn5384xqQ1aoWXA+058RXPxPg6fy4IWvTNh0E263XmFcJlSAwiGgFAW/dAiS6JXm" crossorigin="anonymous"></link>

 const haddleDetele = (event)=>{
  return alert('Are You Sure?');
}

const columns = [
  {
      name: 'ID',
      selector: row => row.Bookid,style:{fontSize:'20px',textAlign:'center'},
      width:'100px',
      sortable:true,
  },
  {
      name: 'Image',
      selector: row => row.imagebook,
      cell:row => <img src={row.imagebook} width={150} alt={row.imagebook}/>,
      width:'250px'
  },
  {
      name: 'Name',
      selector: row => row.Bookname,style:{fontSize:'20px'},
      sortable:true,width:'500px'
  },
  {
      name: 'Status',
      selector: row => row.status,style:{fontSize:'20px'},width:'150px',
      sortable:true,
  },
  {
      name: 'Action',
      cell:({Edit})=>(<a href="../books/delete/<%= data[i].Bookid %>" class="btn btn-success edit"style={{fontSize:'15px'}}>Edit</a> ),width:'150px',
  },
  {
      name: 'Delete',
      cell:({Detele})=>(<a href="../books/delete/<%= data[i].Bookid %>" class="btn btn-danger delete" onClick={haddleDetele} style={{fontSize:'15px'}}>Delete</a> ),width:'150px',
  },

];

function App() {
  const [data, setData] = useState([]);
	const [loading, setLoading] = useState(false);
	const [totalRows, setTotalRows] = useState(0);
	const [page, setPage] = useState(1);
	const [perPage, setPerPage] = useState(10);
  const [sortcolumn, setSortcolumn] = useState('');
  const [sortcolumnDirection, setSortcolumnDirection] = useState('');
  var url = `http://localhost:3000/databasebook?page=${page}&per_page=${perPage}`;
  const [search, setSearch] = useState('');

  if(search){
    url += `&search=${search}`;
  }

  if (sortcolumn){
    url += `&sort_column=${sortcolumn}&sort_direction=${sortcolumnDirection}`
  }

	const fetchData = async () => {
		setLoading(true);

		const response = await axios.get(url);

		setData(response.data.data);
		setTotalRows(response.data.total);
		setLoading(false);
	};

	const handlePageChange = page => {
		setPage(page);
	};

	const handlePerRowsChange = async (newPerPage, page) => {
		setPerPage(newPerPage);
	};

  const handleSort = (column, sortDirection) => {
    setSortcolumn(column.name);
    setSortcolumnDirection(sortDirection);
	};

  const haddleSearchChange = (event) =>{
    setSearch(event.target.value);
  }
  
  const haddleSearchsubmit = (event)=>{
    event.preventDefault(); // ไม่ใช้เปลี่ยนหน้าหรือเข้าหน้ามาใหม่
    fetchData();
  }

	useEffect(() => {
		fetchData(); 
	}, [page, perPage,setSortcolumn,setSortcolumnDirection]);



	return (
    <div>
      <form onSubmit={haddleSearchsubmit}>
        <label style={{paddingTop:'30px',paddingLeft:'10px'}}><h4>Search :</h4>
      <input type="text" name='name' placeholder='Find bookname' onChange={haddleSearchChange} style={{margin:'10px'}} />
        </label>
        <input type="submit" value="submit" ></input>
      </form>
		<DataTable
			title="Book List"
			columns={columns}
			data={data}
			progressPending={loading}
			pagination
			paginationServer
			paginationTotalRows={totalRows}
			onChangeRowsPerPage={handlePerRowsChange}
			onChangePage={handlePageChange}
      onSort={handleSort}
      onAlert={haddleDetele}
		/>
    </div>
	);
}

export default App;
